import CheckboxComponent from '../CheckboxComponent';
import TooltipComponent from '../TooltipComponent';

const ProductCheckboxComponent = ({
  id,
  isChecked,
  labelText,
  tooltipText,
  tooltipClassName,
  tooltipPosition,
  tooltipSize,
}) => {
  return `
    <div class="product__container-checkbox">
      ${CheckboxComponent({
        id,
        labelText,
        isChecked,
      })}

      ${TooltipComponent({
        tooltipText,
        tooltipClassName,
        tooltipPosition,
        tooltipSize,
      })}
    </div>
  `;
};

export default ProductCheckboxComponent;
