// Dependencies
import { tns } from 'tiny-slider/src/tiny-slider';

// Commons
import { Helper } from 'Commons';

// Componentes
import ModalComponent from 'Components/b8one.modalComponent.js';

// Repositories
import MasterdataRepository from 'Repositories/b8one.masterdataRepository.js';
import MoveListSaveRepository from 'Repositories/b8one.moveListSaveRepository.js';

// Services
import CartService from 'Services/b8one.cartService.js';
import StorageService from 'Services/b8one.storageService.js';
import VtexCatalogService from 'Services/b8one.vtexCatalogService.js';

// Utils
import { REAL } from 'Utils/currency/';

import store from '../../../state/store';
import { Creators as productsActions } from '../../../state/ducks/products';

import ProductComponent from './ProductComponent';

class SelectItems {
  constructor() {
    this.products = {};
    this.productInstances = {};
    this.categories = [];
    this.categoriesAmbients = [];
    this.hoistModalInstance = null;
    this.addCustomProductModalInstance = null;
    this.cartService = new CartService();
    this.storageService = new StorageService();
    this.vtexCatalogService = new VtexCatalogService();
    this.helper = new Helper();
    this.masterdataRepository = new MasterdataRepository('CA');
    this.moveListSaveRepository = new MoveListSaveRepository();
    this.orderForm = null;
    this.userMoveSaveList = null;

    this.liftingQuantity = 0;
    this.lifting = 'iamento na origem e destino';

    this.init();
  }

  updateOrderForm(orderForm) {
    if (orderForm) {
      this.orderForm = orderForm;
    }
  }

  async init() {
    this.setToggleSelectedProducts();
    this.handledButtonSaveClick();
    this.setHandledButtonContinueClick();
    this.initModalHoist();

    const [orderForm, categories, categoriesAmbients] = await Promise.all([
      this.cartService.getCurrent(),
      this.getCategories(),
      this.getCategoriesAmbientsFromMasterData(),
    ]);

    this.categories = categories;
    this.categoriesAmbients = categoriesAmbients;

    this.updateOrderForm(orderForm);
    this.getLiftingFromCustomData();

    this.categories = await this.getProductsByCategories();

    this.addCategoriesAmbientToDOM();
    this.setHandledAmbientButtonBackClick();

    if (window.location.search.includes('isSavedList=true')) {
      await this.getUserMoveSaveList();
    }

    this.setProducts();
    this.getProductstInOrderForm();
    this.addProductsToState();
    this.addProductComponentInstances();
    this.addCustomProductsToCategories();

    this.addSectionCategoriesToDOM();
    this.setListenersToProducts();

    // if (window.location.search.includes('isSavedList=true')) {
    //   this.showOnlyUserMoveSaveListProducts();
    // }

    this.initAddCustomProductModal();

    this.categoriesCollapseSections();
    this.setHandledInputSearchChange();
    this.setHandledButtonCleanInputSearchClick();
    this.setHandledButtonClearAllClick();
    this.setHandledCategoriesAmbientClick();
  }

  setProducts() {
    this.categories.forEach(({ products }) => {
      products = products.reduce((obj, product) => {
        const {
          productId,
          productName,
          assignments,
          items,
          ambients,
          similars,
          additionalCharges,
          additionalWeight,
        } = product;
        const { imageUrl, complementName } = items[0];

        const [skuWithoutBox, skuWithBox] = items;

        const skuIdWithBox = skuWithBox?.itemId;
        const skuIdWithoutBox = skuWithoutBox?.itemId;
        const skuId = skuIdWithoutBox;

        return {
          ...obj,
          [productId]: {
            imageUrl,
            productId,
            productName,
            skuId,
            ambients,
            skuIdWithBox,
            skuIdWithoutBox,
            complementName,
            assignments,
            similars,
            additionalCharges,
            additionalWeight,
          },
        };
      }, {});

      this.products = { ...this.products, ...products };
    });
  }

  addProductsToState() {
    Object.values(this.products).forEach(product => {
      const { imageUrl, complementName, active, ...productData } = product;

      const action = productsActions.addProduct(productData);

      store.dispatch(action);
    });
  }

  addProductComponentInstances() {
    Object.values(this.products).forEach(product => {
      const { productId } = product;

      const productInstance = new ProductComponent(
        {
          ...product,
          lifting: this.lifting,
        },
        store
      );

      this.productInstances = {
        ...this.productInstances,
        [productId]: productInstance,
      };
    });
  }

  addCustomProductsToCategories() {
    Object.values(this.products)
      .filter(product => product.isCustom)
      .forEach(product => {
        const categoryID = product.customAttributes['product-category'];

        this.categories = this.categories.map(category => {
          if (category.idHTML !== categoryID) return category;

          const { products } = category;

          return {
            ...category,
            products: [...products, product],
          };
        });
      });
  }

  setListenersToProducts() {
    Object.values(this.productInstances).forEach(productInstance => {
      productInstance.bindEvents();
    });
  }

  initAddCustomProductModal() {
    const productAddCustomModal = document.querySelector(
      '.product-custom-modal'
    );

    if (productAddCustomModal) {
      let currentSectionProducts;

      const inputCustomProductName = document.querySelector(
        '.js-input-custom-product-name'
      );

      this.addCustomProductModalInstance = new ModalComponent({
        modal: productAddCustomModal,
        modalShowCLass: 'wrapper-modal--show',
        openModalCallback: () => {
          setTimeout(() => inputCustomProductName.focus(), 500);
        },
      });

      const addProductCustomButtons = document.querySelectorAll(
        '.product--add-custom .button'
      );

      [...addProductCustomButtons].forEach(button => {
        button.addEventListener('click', () => {
          this.addCustomProductModalInstance.show();

          currentSectionProducts = Helper.getParents(
            button,
            '.section__products'
          )[0];
        });
      });

      const buttonCloseModal = productAddCustomModal.querySelector(
        '.modal__close'
      );
      const productCustomForm = productAddCustomModal.querySelector(
        '.modal__form'
      );

      buttonCloseModal.addEventListener('click', () => {
        this.addCustomProductModalInstance.close();
        productCustomForm.reset();
      });

      productCustomForm.addEventListener('submit', event => {
        event.preventDefault();

        const customAttributes = Helper.getFormData(productCustomForm);
        const categorySection = currentSectionProducts?.parentElement;
        const categoryName = categorySection?.getAttribute('id');

        customAttributes['product-category'] = categoryName;

        const m3 = this.convertCM3ToM3(
          parseInt(customAttributes['product-height']),
          parseInt(customAttributes['product-width']),
          parseInt(customAttributes['product-depth'])
        );

        const customProductItem = this.getCustomsProducts(m3);

        const productInstance = this.createCustomProduct({
          customAttributes,
          customProductItem,
        });

        if (currentSectionProducts) {
          currentSectionProducts.lastElementChild.insertAdjacentHTML(
            'beforebegin',
            productInstance.render()
          );

          productInstance.bindEvents();
        } else {
          const sectionProducts = document.querySelector(
            '.products-category .category__section .section__products'
          );

          const searchResultsProducts = document.querySelector(
            '.search-results__products'
          );

          sectionProducts.lastElementChild.insertAdjacentHTML(
            'beforebegin',
            productInstance.render()
          );

          searchResultsProducts.lastElementChild.insertAdjacentHTML(
            'beforebegin',
            productInstance.render()
          );

          productInstance.bindEvents();
        }

        this.addCustomProductModalInstance.close();
        productCustomForm.reset();
      });
    }
  }

  initModalHoist() {
    const hoistModal = document.querySelector('.hoist-modal');
    const hoistButton = hoistModal.querySelector('.modal__button');
    const hoistInputs = hoistModal.querySelectorAll('.modal__input');

    hoistInputs.forEach(input => {
      input.addEventListener('click', () => {
        const oldInputActive = document.querySelector('.modal__input--active');

        oldInputActive &&
          oldInputActive.classList.remove('modal__input--active');

        input.querySelector('input').checked = true;

        input.classList.add('modal__input--active');

        if (input.querySelector('input').checked) {
          this.lifting = input.querySelector('input').value;
          hoistButton.disabled = false;
        }
      });
    });

    this.hoistModalInstance = new ModalComponent({
      modal: hoistModal,
      modalShowCLass: 'wrapper-modal--show',
    });

    hoistButton.addEventListener('click', async () => {
      hoistButton.classList.add('js-loading');

      hoistInputs.forEach(item => {
        if (item.querySelector('input').checked === true) {
          this.cartService.setSingleCustomDataField(
            'orderinfo',
            'lifting',
            this.lifting
          );
        }
      });

      await this.handledButtonContinueClick(hoistButton);

      this.hoistModalInstance.close();
    });
  }

  getLiftingFromCustomData() {
    const { customData } = this.orderForm;

    if (!customData) return;

    const orderInfoApp = customData.customApps.find(
      customApp => customApp.id === 'orderinfo'
    );

    if (!orderInfoApp) return;

    const { lifting } = orderInfoApp;

    if (lifting) {
      this.lifting = lifting;
    }
  }

  setToggleSelectedProducts(event) {
    const showSelectProductsInput = document.querySelector(
      '#show-select-products'
    );

    showSelectProductsInput.addEventListener(
      'change',
      ({ target: { checked } }) => this.handledToggleSelectedProducts(checked)
    );
  }

  handledToggleSelectedProducts(isSelected) {
    if (window.pageYOffset > 0) {
      window.scrollTo(0, 0);
    }

    const inputSearch = document.querySelector('.js-input-search');

    if (inputSearch.value !== '') {
      this.clearInputSearch();
    }

    if (isSelected) {
      const ambientElement = document.querySelector('.ambient');
      const productsCategory = document.querySelector('.products-category');
      const pageCategoriesElement = document.querySelector('.page__categories');
      const pageSearchResultsAmbientElement = document.querySelector(
        '.js-search-results-ambient'
      );

      ambientElement.classList.add('hide');
      productsCategory.classList.remove('hide');
      pageCategoriesElement.classList.remove('hide');
      pageSearchResultsAmbientElement.classList.add('hide');

      this.clearResultsProducts('.js-search-results-ambient');
    }

    const productsWithoutQuantity = document.querySelectorAll(
      '.product[data-product-quantity="0"]'
    );

    const productAddCustoms = document.querySelectorAll('.product--add-custom');

    const categorySections = document.querySelectorAll('.category__section');

    const categorySectionsToHide = [...categorySections].filter(
      categorySection => {
        const products = [
          ...categorySection.querySelectorAll('.product[data-product-id]'),
        ];

        const totalProductsQuantity = products.reduce((count, product) => {
          let { productQuantity } = product.dataset;

          productQuantity = parseInt(productQuantity);

          return count + productQuantity;
        }, 0);

        console.log(totalProductsQuantity);

        return totalProductsQuantity === 0;
      }
    );

    const categorySectionsToShow = [...categorySections].filter(
      categorySection => {
        const products = [
          ...categorySection.querySelectorAll('.product[data-product-id]'),
        ];

        const totalProductsQuantity = products.reduce((count, product) => {
          let { productQuantity } = product.dataset;

          productQuantity = parseInt(productQuantity);

          return count + productQuantity;
        }, 0);

        console.log(totalProductsQuantity);

        return totalProductsQuantity !== 0;
      }
    );

    [
      ...productsWithoutQuantity,
      ...productAddCustoms,
      ...categorySectionsToHide,
    ].forEach(element => {
      isSelected
        ? element.classList.add('hidden')
        : element.classList.remove('hidden');
    });

    [...categorySectionsToShow].forEach(element => {
      element.classList.add('category__section--show');
    });

    this.toggleNotProductsSelectedMessage(isSelected);
  }

  toggleNotProductsSelectedMessage(showMessage = false) {
    const productsCategoryElement = document.querySelector(
      '.products-category'
    );

    const categorySectionsToHide = [...productsCategoryElement.children].filter(
      categorySection => {
        return categorySection.classList.contains('hidden');
      }
    );

    if (
      showMessage &&
      categorySectionsToHide.length === productsCategoryElement.children.length
    ) {
      productsCategoryElement.classList.add('without-products');
    } else {
      productsCategoryElement.classList.remove('without-products');
    }
  }

  createCustomProduct(productData) {
    let { productId } = productData;

    productId = productId ? productId : Helper.getRandomID();

    const productName = productData.customAttributes['product-name'];

    productData = {
      ...productData,
      productId,
      productName,
      quantity: 1,
      skuId: 25,
      skuIdWithBox: 26,
      skuIdWithoutBox: 25,
      isCustom: true,
      assignments: ['iamento', 'embalagem especial'],
    };

    const action = productsActions.addProduct(productData);

    store.dispatch(action);

    const productInstance = new ProductComponent({
      ...productData,
      productId,
      complementName: productName,
    });

    this.products[productId] = {
      ...productData,
      productId,
      complementName: productName,
    };

    this.productInstances[productId] = productInstance;

    return productInstance;
  }

  categoriesCollapseSections() {
    const categorySections = document.querySelectorAll('.category__section');

    [...categorySections].forEach(categorySection => {
      const sectionContainer = categorySection.querySelector(
        '.section__container'
      );

      const buttonToggle = categorySection.querySelector(
        '.category__button-toggle'
      );

      const buttonToggleIcon = buttonToggle.querySelector(
        '.button-toggle__icon'
      );

      sectionContainer.addEventListener('click', event => {
        const categorySectionActive = document.querySelector(
          '.category__section--show'
        );

        const isVisible = categorySection.classList.contains(
          'category__section--show'
        );

        if (categorySectionActive) {
          const buttonToggleActive = categorySectionActive.querySelector(
            '.category__button-toggle'
          );

          const buttonToggleIconActive = buttonToggleActive.querySelector(
            '.button-toggle__icon'
          );

          categorySectionActive.classList.remove('category__section--show');
          buttonToggleActive.classList.remove('category__button-toggle--open');
          buttonToggleIconActive.classList.replace('fa-minus', 'fa-plus');
        }

        if (!isVisible) {
          categorySection.classList.add('category__section--show');
          buttonToggle.classList.add('category__button-toggle--open');
          buttonToggleIcon.classList.replace('fa-plus', 'fa-minus');
        }
      });
    });
  }

  getLiftingSKUs() {
    const { products } = store.getState();

    this.getLiftingQuantity();

    const liftingQuantity =
      this.lifting === 'iamento na origem e destino'
        ? this.liftingQuantity / 2
        : this.liftingQuantity;

    const skus = [];

    if (
      this.lifting === 'iamento na origem e destino' ||
      this.lifting === 'iamento no origem'
    ) {
      skus.push({
        id: 19,
        skuId: 19,
        seller: 1,
        productId: 10,
        quantity: liftingQuantity,
      });

      Object.values(products).forEach(product => {
        if (product.quantity > 0 && product.requiresLifting) {
          const action = productsActions.updateLiftingQuantity({
            productId: product.productId,
            liftingQuantityOrigin: product.liftingQuantity,
          });

          store.dispatch(action);
        }
      });
    }

    if (
      this.lifting === 'iamento na origem e destino' ||
      this.lifting === 'iamento no destino'
    ) {
      skus.push({
        id: 698,
        skuId: 698,
        seller: 1,
        productId: 10,
        quantity: liftingQuantity,
      });

      Object.values(products).forEach(product => {
        if (product.quantity > 0 && product.requiresLifting) {
          const action = productsActions.updateLiftingQuantity({
            productId: product.productId,
            liftingQuantityDestination: product.liftingQuantity,
          });

          store.dispatch(action);
        }
      });
    }

    return skus;
  }

  getLiftingQuantity() {
    const { products } = store.getState();

    this.liftingQuantity = Object.values(products)
      .filter(({ quantity }) => quantity > 0)
      .reduce((quantity, product) => {
        return quantity + product.liftingQuantity;
      }, 0);

    this.liftingQuantity *=
      this.lifting === 'iamento na origem e destino' ? 2 : 1;
  }

  async saveData() {
    try {
      let { products } = store.getState();

      await window.vtexjs.checkout.removeAllItems();

      const liftingSKUs = this.getLiftingSKUs();

      const productsWithQuantity = Object.values(products).filter(
        ({ quantity }) => quantity !== 0
      );

      const additionalWeightSKUs = this.getAdditionalWeightSKU(
        productsWithQuantity
      );

      const disassemblyAndAssemblySKUs = this.getDisassemblyAndAssemblySKU(
        productsWithQuantity
      );

      products = productsWithQuantity.map(product => ({
        ...product,
        seller: 1,
        id: product.skuId,
      }));

      await this.cartService.checkoutCRUD([
        ...products,
        ...liftingSKUs,
        additionalWeightSKUs,
        disassemblyAndAssemblySKUs,
      ]);

      await Promise.all([
        this.cartService.setSingleCustomDataField(
          'selecteditems',
          'products_data',
          JSON.stringify(productsWithQuantity)
        ),
        this.cartService.setSingleCustomDataField(
          'orderinfo',
          'current_step',
          'select-items'
        ),
        this.updateCustomAppInsurance(),
        this.updateCustomAppSelectedCourier(),
      ]);
    } catch (error) {
      console.error('saveData', error);
    }
  }

  setHandledButtonContinueClick() {
    const buttonContinue = document.querySelector('.js-button-continue');

    buttonContinue.addEventListener('click', e => {
      e.preventDefault();

      const { products } = store.getState();

      const productsArray = Object.values(products);

      buttonContinue.classList.add('js-loading');

      const requiresLifting = productsArray.some(
        product => product.requiresLifting
      );

      const hasProductSelected = productsArray.some(
        product => product.quantity > 0
      );

      if (requiresLifting && hasProductSelected) {
        this.hoistModalInstance.show();
      } else {
        this.handledButtonContinueClick(buttonContinue);
      }
    });
  }

  async handledButtonContinueClick(buttonContinue) {
    this.blockProducts();

    buttonContinue.classList.add('button--disabled');

    await this.saveData();

    const { products } = store.getState();

    const productsWithQuantity = Object.values(products).filter(
      product => product.quantity > 0
    );

    if (productsWithQuantity.length > 0) {
      window.location.assign('/empresa-de-mudanca');
    } else {
      buttonContinue.classList.remove('js-loading');
      buttonContinue.classList.remove('button--disabled');

      this.unlockProducts();

      this.showButtonContinueTooltip();

      setTimeout(this.hideButtonContinueTooltip, 4000);
    }
  }

  showButtonContinueTooltip() {
    const buttonContinue = document.querySelector('.js-button-continue');

    buttonContinue.setAttribute('data-balloon-pos', 'up');
    buttonContinue.setAttribute('data-balloon-visible', true);
    buttonContinue.setAttribute('data-balloon-length', 'fit');
    buttonContinue.setAttribute(
      'aria-label',
      'Para continuar com a mudana  necessario ter pelo menos 1 item selecionado'
    );
  }

  hideButtonContinueTooltip() {
    const buttonContinue = document.querySelector('.js-button-continue');

    buttonContinue.removeAttribute('aria-label');
    buttonContinue.removeAttribute('data-balloon-pos');
    buttonContinue.removeAttribute('data-balloon-visible');
    buttonContinue.removeAttribute('data-balloon-length');
  }

  handledButtonSaveClick() {
    const buttonSaveElement = document.querySelector('.js-button-save');
    const buttonSaveIconElement = document.querySelector(
      '.js-button-save-icon'
    );
    const buttonSaveIconLoadingElement = document.querySelector(
      '.js-button-save-icon-loading'
    );

    const buttonContinue = document.querySelector('.js-button-continue');

    buttonSaveElement.addEventListener('click', async event => {
      document.dispatchEvent(window.initSaveCustomDataEvent);

      this.blockProducts();

      buttonSaveIconElement.classList.add('hide');
      buttonSaveIconLoadingElement.classList.remove('hide');

      buttonContinue.classList.add('button--disabled');

      await this.saveData();

      document.dispatchEvent(window.endSaveCustomDataEvent);

      this.unlockProducts();

      buttonContinue.classList.remove('button--disabled');

      buttonSaveIconElement.classList.remove('hide');
      buttonSaveIconLoadingElement.classList.add('hide');
    });
  }

  blockProducts() {
    const sectionProducts = [
      ...document.querySelectorAll('.section__products'),
    ];

    sectionProducts.forEach(sectionProduct => {
      sectionProduct.classList.add('section__products--locked');
    });
  }

  unlockProducts() {
    const sectionProducts = [
      ...document.querySelectorAll('.section__products'),
    ];

    sectionProducts.forEach(sectionProduct => {
      sectionProduct.classList.remove('section__products--locked');
    });
  }

  async getCategories() {
    const { data } = await this.vtexCatalogService.getAllCategories();

    const [categories] = data;

    const categoriesIDBlacklist = [22];

    const categoriesFiltered = categories.children
      .filter(({ id }) => !categoriesIDBlacklist.includes(id))
      .map(({ id, name, url }) => {
        const idHTML = url.split('/').slice(-1)[0];

        return {
          id,
          name,
          url,
          idHTML,
          products: [],
        };
      });

    return categoriesFiltered;
  }

  async getProductsByCategories() {
    return Promise.all(
      this.categories.map(async category => {
        let products = await this.vtexCatalogService.getAllProductsByCategories(
          1,
          category.id
        );

        products = products.map(
          ({
            productName,
            productId,
            items,
            description,
            Features,
            Ambientes = [],
            similar = [],
            ...product
          }) => {
            items = items.map(({ itemId, images, complementName }) => ({
              itemId,
              complementName,
              imageUrl: images[0].imageUrl,
            }));

            const ambients = Ambientes.map(ambient => {
              return Helper.removeAccents(ambient)
                .toLowerCase()
                .replace(/\s/g, '-');
            });

            const similars = similar.map(similarItem => {
              return Helper.removeAccents(similarItem)
                .toLowerCase()
                .replace(/\s/g, '-');
            });

            const additionalCharges = product['Cobranas Adicionais'] || [];

            return {
              items,
              ambients,
              productId,
              productName,
              description,
              assignments: Features,
              similars,
              additionalCharges,
              additionalWeight: additionalCharges.includes('Peso Adicional'),
            };
          }
        );

        return {
          ...category,
          products,
        };
      })
    );
  }

  createAddCustomProductElement() {
    return `
      <article class="product product--add-custom">
        <div class="product__container">
          <i class="fa-question fal product__icon"></i>

          <h2 class="product__title">No encontrou um item?</h2>

          <p class="product__paragraph">Clique no boto e adicione manualmente.</p>
        </div>

        <button class="button button--secondary">Adicionar item</button>
      </article>
    `;
  }

  createProducts(products) {
    products = products
      .map(({ productId }) => this.productInstances[productId].render())
      .join('');

    products += this.createAddCustomProductElement();

    return products;
  }

  createSectionCategories() {
    const sectionCategories = this.categories
      .map(({ name, idHTML, products }, index) => {
        return `
          <section class="category__section ${
            index === 0 ? 'category__section--show' : ''
          }" id="${idHTML}">
            <div class="section__container">
              <h2 class="category__name">${name}</h2>

              <button class="category__button-toggle ${
                index === 0 ? 'category__button-toggle--open' : ''
              }">
                <i class="fas button-toggle__icon ${
                  index === 0 ? 'fa-minus' : 'fa-plus'
                }"></i>
                
                <span class="button-toggle__text">
                  ver menos
                </span>
              </button>
            </div>

            <div class="section__products">
              ${this.createProducts(products)}
            </div>
          </section>
        `;
      })
      .join('');

    return sectionCategories;
  }

  addSectionCategoriesToDOM() {
    const productsCategory = document.querySelector('.products-category');

    productsCategory.innerHTML = this.createSectionCategories();
  }

  getProductstInOrderForm() {
    const { customData } = this.orderForm;

    if (!customData) return;

    const selectedItemsCustomApp = customData.customApps.find(
      customApp => customApp.id === 'selecteditems'
    );

    if (!selectedItemsCustomApp) return;

    const products_data = JSON.parse(
      selectedItemsCustomApp.fields.products_data
    );

    const products = products_data.reduce((obj, product) => {
      let productCustom = {};
      const { productId } = product;

      const productInitialData =
        productId in this.products ? this.products[productId] : {};

      if (product.isCustom) {
        const firstCategory = this.categories[0];

        const productName = product.customAttributes['product-name'];
        const productCategory =
          product.customAttributes['product-category'] || firstCategory.idHTML;

        product.customAttributes['product-category'] = productCategory;

        productCustom = {
          productName,
          complementName: productName,
          assignments: ['iamento', 'embalagem especial'],
        };
      }

      return {
        ...obj,
        [productId]: {
          ...productInitialData,
          ...product,
          ...productCustom,
        },
      };
    }, {});

    this.products = {
      ...this.products,
      ...products,
    };
  }

  /**
   * Add item to cart
   * @param {Number} height - height of the product.
   * @param {Number} width - width of the product.
   * @param {Number} deph - deph of the product.
   * @return {Number} - return the cm unit to m unit.
   */
  convertCM3ToM3(height, width, deph) {
    // 1 m  representado da seguinte forma em cm = 1000000 cm
    const M3EqualCM3 = 1000000;

    return (height * width * deph) / M3EqualCM3;
  }

  setHandledInputSearchChange() {
    const searchInput = document.querySelector('.js-input-search');

    const buttonClearInputSearch = document.querySelector(
      '.js-button-clear-input-search'
    );

    const pageCategoriesElement = document.querySelector('.page__categories');
    const ambientElement = document.querySelector('.ambient');

    const pageSearchResultsElement = document.querySelector(
      '.js-search-results'
    );

    const pageSearchResultsQueryTextElement = pageSearchResultsElement.querySelector(
      '.js-search-query'
    );

    const pageSearchResultsSuggestionsElement = document.querySelector(
      '.js-search-results-suggestions'
    );

    const pageSearchResultsAmbientElement = document.querySelector(
      '.js-search-results-ambient'
    );

    const productsCategory = document.querySelector('.products-category');

    let timeoutProducts;

    searchInput.addEventListener('input', ({ target }) => {
      const { value } = target;

      if (timeoutProducts) clearTimeout(timeoutProducts);

      this.clearResultsProducts();
      this.clearResultsSuggestionsProducts();

      if (window.scrollY > 0) window.scrollTo(0, 0);

      if (value !== '' && value.length > 1) {
        productsCategory.classList.add('hide');
        pageCategoriesElement.classList.add('hide');
        buttonClearInputSearch.classList.add('search__button-clear--show');
        ambientElement.classList.add('hide');
        pageSearchResultsElement.classList.remove('hide');
        pageSearchResultsAmbientElement.classList.add('hide');
        pageSearchResultsSuggestionsElement.classList.add('hide');

        pageSearchResultsQueryTextElement.textContent = `${value}`;

        timeoutProducts = setTimeout(() => {
          const products = Object.values(this.products).filter(product => {
            return product.complementName
              .toLowerCase()
              .includes(value.toLowerCase());
          });

          let similarProducts = [];

          if (products.length > 0) {
            const productWithSimilar = products.find(
              product => product.similars[0]
            );

            if (productWithSimilar) {
              const { similars } = productWithSimilar;
              similarProducts = Object.values(this.products)
                .filter(product => {
                  return !products.find(
                    productItem => productItem.productId === product.productId
                  );
                })
                .filter(product => {
                  return similars.some(similarItem => {
                    return product.similars.includes(similarItem);
                  });
                });
            }
          }

          if (similarProducts.length > 0) {
            pageSearchResultsSuggestionsElement.classList.remove('hide');
          }

          this.updateResultsProducts(products);
          this.updateResultsSuggestionsProducts(similarProducts);
        }, 500);
      } else {
        productsCategory.classList.remove('hide');
        pageCategoriesElement.classList.remove('hide');
        buttonClearInputSearch.classList.remove('search__button-clear--show');

        pageSearchResultsElement.classList.add('hide');
        pageSearchResultsSuggestionsElement.classList.add('hide');

        pageSearchResultsQueryTextElement.textContent = '';

        this.showHiddenCategoriesAndProducts();
      }
    });
  }

  updateResultsProducts(products, parentSelector = '') {
    if (products.length === 0) return;

    const resultsProducts = document.querySelector(
      `${parentSelector} .search-results__products`
    );

    const productsHTML = products.map(({ productId }) => {
      const productHTML = this.productInstances[productId].render();

      return productHTML;
    });

    resultsProducts.innerHTML = productsHTML.join('');
    resultsProducts.innerHTML += this.createAddCustomProductElement();

    products.forEach(({ productId }) =>
      this.productInstances[productId].bindEvents()
    );

    const addProductCustomButton = resultsProducts.querySelector(
      '.product--add-custom .button'
    );

    addProductCustomButton.addEventListener('click', () => {
      this.addCustomProductModalInstance.show();
    });
  }

  updateResultsSuggestionsProducts(products) {
    if (products.length === 0) return;

    const resultsProducts = document.querySelector(
      `.js-search-results-suggestions .search-results__products`
    );

    const productsHTML = products.map(({ productId }) => {
      const productHTML = this.productInstances[productId].render();

      return productHTML;
    });

    resultsProducts.innerHTML = productsHTML.join('');

    products.forEach(({ productId }) =>
      this.productInstances[productId].bindEvents()
    );
  }

  clearResultsSuggestionsProducts() {
    this.clearResultsProducts('.js-search-results-suggestions');
  }

  clearResultsProducts(parentElement = '') {
    const resultsProducts = document.querySelector(
      `${parentElement} .search-results__products`
    );

    resultsProducts.classList.add('empty-search');
    resultsProducts.innerHTML =
      '<p>Desculpe, no encontramos o resultado para sua busca</p>';
  }

  getCustomsProducts(number) {
    const intenger = Math.trunc(number);
    const decimal = number % 1;
    let total = intenger * 10;

    if (decimal) {
      const decimalFirstNumber = parseInt(
        decimal.toString().split('.')[1][0],
        10
      );

      total += decimalFirstNumber;
    }

    total = total === 0 ? 1 : total;

    return {
      id: 25,
      seller: 1,
      productId: 12,
      quantity: total,
    };
  }

  async updateCustomAppInsurance() {
    const { customData } = await this.cartService.getCurrent();

    if (!customData) return;

    const insuranceApp = customData.customApps.filter(customApp => {
      return (
        customApp.id === 'insurance' &&
        !['skipped', 'none'].includes(customApp.fields.items)
      );
    })[0];

    if (!insuranceApp) return;

    const { items } = insuranceApp.fields;

    const insuranceItems = JSON.parse(items);

    const newInsuranceItems = Object.values(insuranceItems).filter(item => {
      return this.products[item.productId].quantity > 0;
    });

    const newInsuranceItemsObject = newInsuranceItems.reduce((obj, item) => {
      return { ...obj, [item.productId]: item };
    }, {});

    const newTotal = Object.values(newInsuranceItems).reduce(
      (total, product) => {
        return total.add(product.totalInsuranceFormatted);
      },
      REAL('R$ 0,00')
    );

    await this.cartService.setMultipleCustomDataFields('insurance', {
      total: newTotal.format(),
      items: JSON.stringify(newInsuranceItemsObject),
    });
  }

  async updateCustomAppSelectedCourier() {
    const {
      customData,
      items,
      totalizers,
    } = await this.cartService.getCurrent();

    if (!customData) return;

    const totalItemsWithDiscounts = totalizers
      .filter(total => total.id !== 'Shipping')
      .reduce((total, totalizer) => total + totalizer.value, 0);

    const selectedCourierApp = customData.customApps.filter(
      customApp => customApp.id === 'selectedcourier'
    )[0];

    if (!selectedCourierApp) return;

    const orderinfoApp = customData.customApps.filter(
      customApp => customApp.id === 'orderinfo'
    )[0];

    const { to_address_zipcode } = orderinfoApp.fields;

    const products = items.map(({ id, seller, quantity }) => ({
      id,
      seller,
      quantity,
    }));

    const insuranceSKU = items.filter(({ id }) => parseInt(id) === 35)[0];

    const { courier_data } = selectedCourierApp.fields;
    let transporterSelected = JSON.parse(courier_data);

    let { logisticsInfo } = await this.cartService.simulateShipping(
      products,
      to_address_zipcode
    );

    logisticsInfo = logisticsInfo.filter(logisticInfo => {
      const ids = transporterSelected.courierIds.split(';');

      logisticInfo.slas = logisticInfo.slas.filter(sla => {
        return ids.includes(sla.deliveryIds[0].courierId);
      });

      return logisticInfo;
    });

    transporterSelected = this.getDeliveryTypes(
      logisticsInfo,
      transporterSelected
    );

    let newPrice = logisticsInfo.reduce((total, logisticInfo) => {
      const currentSLA = logisticInfo.slas.filter(
        sla => sla.id === transporterSelected.deliveryTypeSelected.slaName
      )[0];

      return total + currentSLA.price;
    }, 0);

    newPrice += insuranceSKU
      ? totalItemsWithDiscounts + -insuranceSKU.manualPrice
      : totalItemsWithDiscounts;

    const newPriceFormatted = this.helper.formatPrice(newPrice);
    const { deliveryTypeSelected, deliveryTypes } = transporterSelected;

    transporterSelected.deliveryTypeSelected = {
      ...deliveryTypeSelected,
      price: newPrice,
      priceFormatted: newPriceFormatted,
    };

    transporterSelected.deliveryTypes = deliveryTypes.map(deliveryType => {
      if (deliveryType.slaName === deliveryTypeSelected.slaName) {
        deliveryType = transporterSelected.deliveryTypeSelected;
      }

      return deliveryType;
    });

    transporterSelected = this.orderDeliveryTypesByLessPrice(
      transporterSelected
    );

    await this.cartService.setSingleCustomDataField(
      'selectedcourier',
      'courier_data',
      JSON.stringify(transporterSelected)
    );
  }

  getDeliveryTypes(logisticsInfo, transporterSelected) {
    const transporterVariations = this.getTransporterVariations(logisticsInfo);

    delete transporterSelected.deliveryType;
    delete transporterSelected.deliveryTypes;

    logisticsInfo[0].slas.forEach(({ id, name, deliveryIds }) => {
      const key = id.toLowerCase().replace(/\s/g, '');
      const [courierName, courierType] = id.split(' - ');

      const { courierId } = deliveryIds[0];

      const { price } = transporterVariations[key];

      const priceFormatted = this.helper.formatPrice(price);

      const courier = [
        {
          price,
          priceFormatted,
          id: courierId,
          type: courierType,
          slaName: name,
        },
      ];

      if (transporterSelected.deliveryTypes) {
        transporterSelected.deliveryTypes = [
          ...courier,
          ...transporterSelected.deliveryTypes,
        ];
      } else {
        transporterSelected.deliveryTypes = courier;
      }
    });

    return transporterSelected;
  }

  getTransporterVariations(logisticsInfo) {
    const transporterVariations = {};

    logisticsInfo.forEach(({ slas }) => {
      slas.forEach(({ id, price, name }) => {
        const key = id.toLowerCase().replace(/\s/g, '');

        if (transporterVariations[key]) {
          transporterVariations[key].price += price;
        } else {
          transporterVariations[key] = {
            price,
            name,
          };
        }
      });
    });

    return transporterVariations;
  }

  orderDeliveryTypesByLessPrice(transporterSelected) {
    transporterSelected.deliveryTypes.sort((a, b) => a.price - b.price);

    return transporterSelected;
  }

  setHandledButtonCleanInputSearchClick() {
    const buttonnClearInputSearch = document.querySelector(
      '.js-button-clear-input-search'
    );

    buttonnClearInputSearch.addEventListener('click', e =>
      this.clearInputSearch()
    );
  }

  clearInputSearch() {
    const eventChange = new Event('input', { bubbles: true });
    const inputSearch = document.querySelector('.js-input-search');

    inputSearch.value = '';

    inputSearch.dispatchEvent(eventChange);
  }

  async removeProducts() {
    await window.vtexjs.checkout.removeAllItems();
    await this.cartService.removeSingleCustomDataField('insurance', 'items');
    await this.cartService.removeSingleCustomDataField('insurance', 'total');
    await this.cartService.removeSingleCustomDataField(
      'selecteditems',
      'products_data'
    );
    await this.cartService.removeSingleCustomDataField(
      'selectedcourier',
      'courier_data'
    );
    await this.cartService.removeSingleCustomDataField(
      'orderinfo',
      'lifting_total_price'
    );
    await this.cartService.removeSingleCustomDataField(
      'orderinfo',
      'special_packaging_total_price'
    );

    store.dispatch(productsActions.resetProductsQuantity());
  }

  setHandledButtonClearAllClick() {
    const buttonClearAllElement = document.querySelector(
      '.js-button-clear-all'
    );

    const buttonClearAllIconElement = document.querySelector(
      '.js-button-clear-all-icon'
    );

    const buttonClearAllIconLoadingElement = document.querySelector(
      '.js-button-clear-all-icon-loading'
    );

    buttonClearAllElement &&
      buttonClearAllElement.addEventListener('click', async () => {
        this.blockProducts();

        buttonClearAllIconElement.classList.add('hide');
        buttonClearAllIconLoadingElement.classList.remove('hide');

        await this.removeProducts();

        this.unlockProducts();

        alert('Todos os itens da lista foram excludos!');

        buttonClearAllIconElement.classList.remove('hide');
        buttonClearAllIconLoadingElement.classList.add('hide');
      });
  }

  async getCategoriesAmbientsFromMasterData() {
    const fields =
      'category_name,category_description,category_image,category_id,id';

    try {
      const response = await this.masterdataRepository.search(
        `_where=category_active=Sim&_fields=${fields}`
      );

      const categories = response.map(async category => {
        category.category_image_url = await this.masterdataRepository.getAttachmentUrl(
          category.id,
          'category_image',
          category.category_image
        );

        return category;
      });

      return Promise.all(categories);
    } catch (error) {
      console.error(error);
    }
  }

  createCategoryAmbientElement({
    category_id,
    category_name,
    category_image_url,
  }) {
    return `
      <section class="category" data-category-ambient-id="${category_id.toLowerCase()}">
        <div class="category__container">
          <figure class="category__figure">
            <img 
              class="category__img"
              src="${category_image_url}"
              alt="${category_name}"
            />
          </figure>
          
          <h4 class="category__name">${category_name}</h4>
        </div>
      </section>
    `;
  }

  addCategoriesAmbientToDOM() {
    const categoriesItemsElement = document.querySelector(
      '.page__categories .categories__items'
    );

    if (!categoriesItemsElement) return;

    const categoriesAmbientsHTML = this.categoriesAmbients
      .map(this.createCategoryAmbientElement)
      .join('');

    categoriesItemsElement.innerHTML = categoriesAmbientsHTML;

    tns({
      container: '.page__categories .categories__items',
      items: 5,
      slideBy: 'page',
      autoplay: false,
      controls: false,
      center: false,
      autoWidth: true,
      nav: false,
      loop: false,
      preventScrollOnTouch: 'auto',
    });
  }

  setHandledCategoriesAmbientClick() {
    const productsCategoryElement = document.querySelector(
      '.products-category'
    );

    const searchResultsAmbientElement = document.querySelector(
      '.js-search-results-ambient'
    );

    const categoriesItems = [
      ...document.querySelector('.page__categories .categories__items')
        .children,
    ];

    categoriesItems.forEach(item => {
      item.addEventListener('click', () => {
        const { categoryAmbientId } = item.dataset;

        this.clearInputSearch();
        this.updateAmbientElement(categoryAmbientId);
        this.clearResultsProducts('.js-search-results-ambient');

        const categorySections = document.querySelectorAll(
          '.category__section'
        );

        categorySections.forEach(categorySection => {
          const products = [
            ...categorySection.querySelectorAll(
              '.product:not(.product--add-custom)'
            ),
          ];

          products.forEach(product => {
            const { productCategoriesAmbients } = product.dataset;

            const showProduct = productCategoriesAmbients.includes(
              categoryAmbientId
            );

            if (!showProduct) {
              product.classList.add('hide');
            }
          });

          const hideAllProducts =
            categorySection.querySelectorAll(
              '.product:not(.hide):not(.product--add-custom)'
            ).length === 0;

          if (hideAllProducts) {
            categorySection.classList.add('hide');
          }
        });

        const visibleProducts = [
          ...document.querySelectorAll(
            '.product:not(.hide):not(.product--add-custom)'
          ),
        ];

        const products = visibleProducts.map(product => {
          const { productId } = product.dataset;

          return { productId };
        });

        productsCategoryElement.classList.add('hide');

        searchResultsAmbientElement.classList.remove('hide');
        this.updateResultsProducts(products, '.js-search-results-ambient');
      });
    });
  }

  updateAmbientElement(categoryAmbientId) {
    const currentCategory = this.categoriesAmbients.find(
      ({ category_id }) => category_id === categoryAmbientId
    );

    const pageCategoriesElement = document.querySelector('.page__categories');
    const ambientElement = document.querySelector('.ambient');
    const ambientImageElement = ambientElement.querySelector('.ambient__image');
    const ambientNameElement = ambientElement.querySelector('.ambient__name');
    const ambientDescription = ambientElement.querySelector(
      '.ambient__description'
    );

    ambientImageElement.style.backgroundImage = `url(${currentCategory.category_image_url})`;
    ambientNameElement.textContent = currentCategory.category_name;
    ambientDescription.textContent =
      currentCategory.category_description !== '-'
        ? currentCategory.category_description
        : '';

    pageCategoriesElement.classList.add('hide');
    ambientElement.classList.remove('hide');
  }

  setHandledAmbientButtonBackClick() {
    const pageCategoriesElement = document.querySelector('.page__categories');
    const ambientElement = document.querySelector('.ambient');
    const ambientButtonBackElement = document.querySelector(
      '.js-ambient-button-back'
    );

    const productsCategoryElement = document.querySelector(
      '.products-category'
    );

    const pageSearchResultsAmbientElement = document.querySelector(
      '.js-search-results-ambient'
    );

    ambientButtonBackElement.addEventListener('click', () => {
      this.clearResultsProducts('.js-search-results-ambient');

      pageCategoriesElement.classList.remove('hide');
      productsCategoryElement.classList.remove('hide');
      ambientElement.classList.add('hide');
      pageSearchResultsAmbientElement.classList.add('hide');

      this.showHiddenCategoriesAndProducts();
    });
  }

  showHiddenCategoriesAndProducts() {
    const hiddenElements = [
      ...document.querySelectorAll('.category__section.hide'),
      ...document.querySelectorAll('.category__section .product.hide'),
    ];

    hiddenElements.forEach(hiddenElement => {
      hiddenElement.classList.remove('hide');
    });
  }

  async getUserMoveSaveList() {
    try {
      const orderInfoApp = this.orderForm.customData?.customApps.find(
        customApp => customApp.id === 'orderinfo'
      );

      if (!orderInfoApp) return;

      const { moveListSaveSelected: DocumentId } = orderInfoApp.fields;

      if (DocumentId === 'custom list') return;

      this.userMoveSaveList = await this.moveListSaveRepository.getListByDocumentId(
        DocumentId
      );

      if (!this.userMoveSaveList.hasOwnProperty('id')) return;

      await this.updateProductsWithUserMoveSaveListProducts();
    } catch (error) {
      this.userMoveSaveList = null;
    }
  }

  async updateProductsWithUserMoveSaveListProducts() {
    if (this.userMoveSaveList?.isTemplate) return;

    this.updateOrderForm(await this.removeProducts());

    const listItems = JSON.parse(this.userMoveSaveList.listItems);

    const products = listItems.map(({ skuId, quantity }) => ({
      quantity,
      id: skuId,
      seller: 1,
    }));

    this.updateOrderForm(
      await this.cartService.setSingleCustomDataField(
        'selecteditems',
        'products_data',
        this.userMoveSaveList.listItems
      )
    );

    this.updateOrderForm(await vtexjs.checkout.addToCart(products));
  }

  showOnlyUserMoveSaveListProducts() {
    if (!this.userMoveSaveList.hasOwnProperty('id')) return;

    const showSelectProductsInput = document.querySelector(
      '#show-select-products'
    );

    showSelectProductsInput.checked = true;

    this.handledToggleSelectedProducts(true);
  }

  getAdditionalWeightSKU(products) {
    const totalQuantity = products
      .filter(product => product.additionalWeight)
      .reduce((total, { quantity }) => total + quantity, 0);

    return {
      id: 696,
      seller: 1,
      quantity: totalQuantity,
    };
  }

  getDisassemblyAndAssemblySKU(products) {
    const totalQuantity = products
      .filter(
        ({ requiresAssembly, requiresDisassembly }) =>
          requiresAssembly && requiresDisassembly
      )
      .reduce((total, { quantity }) => total + quantity, 0);

    return {
      id: 699,
      seller: 1,
      quantity: totalQuantity,
    };
  }
}

