import questionMarkIcon from '../../../../../svgs/question-marks';

const TooltipComponent = ({
  tooltipClassName,
  tooltipPosition,
  tooltipSize,
  tooltipText,
}) => {
  return `
    <button 
      aria-label="{${tooltipText}}"
      class="tooltip ${tooltipClassName}" 
      data-balloon-pos="${tooltipPosition}" 
      data-balloon-length="${tooltipSize}"
    >
      ${questionMarkIcon}
    </button>
  `;
};

export default TooltipComponent;
