const CheckboxComponent = ({ id, labelText, isChecked }) => {
  return `
    <label for="${id}">
      <div class="checkbox">
        <input type="checkbox" id="${id}" ${isChecked ? 'checked' : ''}/>

        <i class="far fa-check checkbox__icon"></i>

        <p class="checkbox__label">${labelText}</p>
      </div>
    </label>
  `;
};

export default CheckboxComponent;
