//movize/products
import { createActions, createReducer } from 'reduxsauce';

const INITIAL_STATE = {};

export const { Types, Creators } = createActions(
  {
    addProduct: {
      productId: null,
      skuId: null,
      skuIdWithBox: null,
      skuIdWithoutBox: null,
      quantity: 0,
      liftingQuantity: 0,
      liftingQuantityOrigin: 0,
      liftingQuantityDestination: 0,
      specialPackaging: false,
      requiresLifting: false,
      isCustom: false,
      customAttributes: null,
      customProductItem: null,
      additionalWeight: false,
      additionalCharges: [],
      requiresAssembly: false,
      requiresDisassembly: false,
    },
    updateProductQuantity: ['productId', 'quantity'],
    updateLiftingQuantity: {
      productId: null,
      liftingQuantity: 0,
      liftingQuantityOrigin: 0,
      liftingQuantityDestination: 0,
    },
    setRequiresLifting: ['productId', 'requiresLifting'],
    setSpecialPackaging: ['productId', 'specialPackaging'],
    updateSkuId: ['productId', 'skuId'],
    resetProductsQuantity: '',
    updateRequiresDisassemblyAndAssembly: [
      'productId',
      'requiresAssembly',
      'requiresDisassembly',
    ],
  },
  {}
);

export const setProduct = (state = INITIAL_STATE, action) => {
  const { productId } = action;
  const { type, ...payload } = action;

  return {
    ...state,
    [productId]: {
      ...payload,
    },
  };
};

export const updateProductQuantity = (state = INITIAL_STATE, action) => {
  let { productId, quantity } = action;
  const currentProduct = state[productId];

  if (parseInt(quantity) < 0 || quantity === '') {
    quantity = 0;
  }

  return {
    ...state,
    [productId]: {
      ...currentProduct,
      quantity,
    },
  };
};

export const updateLiftingQuantity = (state = INITIAL_STATE, action) => {
  const { productId } = action;
  const { type, ...payload } = action;
  const currentProduct = state[productId];

  return {
    ...state,
    [productId]: {
      ...currentProduct,
      ...payload,
    },
  };
};

export const setRequiresLifting = (state = INITIAL_STATE, action) => {
  const { productId, requiresLifting } = action;
  const currentProduct = state[productId];

  return {
    ...state,
    [productId]: {
      ...currentProduct,
      requiresLifting,
    },
  };
};

export const setSpecialPackaging = (state = INITIAL_STATE, action) => {
  const { productId, specialPackaging } = action;
  const currentProduct = state[productId];

  return {
    ...state,
    [productId]: {
      ...currentProduct,
      specialPackaging,
    },
  };
};

export const updateSkuId = (state = INITIAL_STATE, action) => {
  const { productId, skuId } = action;
  const currentProduct = state[productId];

  if (currentProduct.isCustom) {
    currentProduct.customProductItem = {
      ...currentProduct.customProductItem,
      id: skuId,
    };
  }

  return {
    ...state,
    [productId]: {
      ...currentProduct,
      skuId,
    },
  };
};

export const resetProductsQuantity = (state = INITIAL_STATE, action) => {
  return Object.entries(state).reduce(
    (products, [productId, product]) => ({
      ...products,
      [productId]: {
        ...product,
        quantity: 0,
        liftingQuantity: 0,
        requiresLifting: false,
        specialPackaging: false,
        liftingQuantityOrigin: 0,
        liftingQuantityDestination: 0,
      },
    }),
    {}
  );
};

export const updateRequiresDisassemblyAndAssembly = (
  state = INITIAL_STATE,
  action
) => {
  const { productId, requiresAssembly, requiresDisassembly } = action;
  const currentProduct = state[productId];

  return {
    ...state,
    [productId]: {
      ...currentProduct,
      requiresAssembly,
      requiresDisassembly,
    },
  };
};

const HANDLERS = {
  [Types.ADD_PRODUCT]: setProduct,
  [Types.UPDATE_PRODUCT_QUANTITY]: updateProductQuantity,
  [Types.UPDATE_LIFTING_QUANTITY]: updateLiftingQuantity,
  [Types.SET_REQUIRES_LIFTING]: setRequiresLifting,
  [Types.SET_SPECIAL_PACKAGING]: setSpecialPackaging,
  [Types.UPDATE_SKU_ID]: updateSkuId,
  [Types.RESET_PRODUCTS_QUANTITY]: resetProductsQuantity,
  [Types.UPDATE_REQUIRES_DISASSEMBLY_AND_ASSEMBLY]: updateRequiresDisassemblyAndAssembly,
};

export default createReducer(INITIAL_STATE, HANDLERS);
