import watch from 'redux-watch';

import { Creators as productsActions } from '../../../../state/ducks/products';

import ProductCheckboxComponent from './ProductCheckboxComponent';
import CustomProductImageComponent from './CustomProductImageComponent';
import ProductImageComponent from './ProductImageComponent';
import ProductDescriptionComponent from './ProductDescriptionComponent';
import ProductActionsComponent from './ProductActionsComponent';

const liftingTooltipText = `Iamento de moveis e bens em geral so necessrios quando no h condies para que seus bens sejam levados por escada ou elevador. Se no tem certeza se o iamento  necessrio ou no, no se preocupe. Nossos consultores o orientaro antes da mudana.`;

const specialPackagingTooltipText = `Para itens frgeis como televises, obra de arte, tampos de mesa de vidro ou de marmore, lustres, taas de cristal etc  sempre recomendado utilizar embalagens da madeira feitas sob medida para seu bem. Se no tem certeza se a embalagem especial  necessria ou no, no se preocupe. Nossos consultores o orientaro antes da mudana.`;

const disassemblyAndAssemblyTooltipText = `Lorem ipsum dolor sit amet consectetur, adipisicing elit. Velit voluptas quos doloribus nostrum, odio autem distinctio, enim rerum modi consequuntur laboriosam sapiente voluptate non, praesentium dolorem quibusdam quae reiciendis porro?`;

export default class ProductComponent {
  constructor(
    {
      productId,
      productName,
      complementName,
      imageUrl,
      assignments,
      ambients,
      description,
      similars,
      lifting,
    },
    store
  ) {
    this.productId = productId;
    this.productName = productName;
    this.complementName = complementName.toLowerCase();
    this.imageUrl = imageUrl;
    this.assignments = assignments;
    this.ambients = ambients;
    this.similars = similars;
    this.lifting = lifting;
    this.store = store;

    this.watchState();
  }

  getState = () => this.store.getState().products[this.productId];

  handledAddQuantityProduct = event => {
    const { quantity } = this.getState();

    const action = productsActions.updateProductQuantity(
      this.productId,
      quantity + 1
    );

    this.store.dispatch(action);
  };

  handledMinusQuantityProduct = event => {
    const { quantity } = this.getState();

    const action = productsActions.updateProductQuantity(
      this.productId,
      quantity - 1
    );

    this.store.dispatch(action);
  };

  handledProductQuantityChange = event => {
    const { value } = event.target;

    const action = productsActions.updateProductQuantity(this.productId, value);

    this.store.dispatch(action);
  };

  handledSpecialPackagingProduct = event => {
    const { checked } = event.target;

    const action = productsActions.setSpecialPackaging(this.productId, checked);

    this.store.dispatch(action);
  };

  handledLiftingProduct = event => {
    const { checked } = event.target;

    const action = productsActions.setRequiresLifting(this.productId, checked);

    this.store.dispatch(action);
  };

  handledDisassemblyAndAssembly = event => {
    const { checked } = event.target;

    const action = productsActions.updateRequiresDisassemblyAndAssembly(
      this.productId,
      checked,
      checked
    );

    this.store.dispatch(action);
  };

  bindEvents = () => {
    const productElements = [
      ...document.querySelectorAll(
        `.product[data-product-id="${this.productId}"]`
      ),
    ];

    productElements.forEach(productElement =>
      this.productHandleEvents(productElement)
    );
  };

  productHandleEvents = productElement => {
    const defaultEventsConfig = {
      passive: true,
    };

    const buttonMinusQuantity = productElement.querySelector(
      '.product__quantity--minus'
    );
    const buttonAddQuantity = productElement.querySelector(
      '.product__quantity--add'
    );

    const inputLiftingCheckbox = productElement.querySelector(
      '[id$="lifting"]'
    );
    const inputSpecialPackagingCheckbox = productElement.querySelector(
      '[id$="special-packaging"]'
    );
    const inputDisassemblyAndAssemblyCheckbox = productElement.querySelector(
      '[id$="disassembly-and-assembly"]'
    );

    const inputProductQuantity = productElement.querySelector(
      '.product__quantity'
    );

    buttonMinusQuantity.addEventListener(
      'click',
      this.handledMinusQuantityProduct,
      defaultEventsConfig
    );

    buttonAddQuantity.addEventListener(
      'click',
      this.handledAddQuantityProduct,
      defaultEventsConfig
    );

    inputProductQuantity.addEventListener('keypress', event => {
      var regex = new RegExp('^[0-9]+$');
      var key = String.fromCharCode(!event.charCode ? event.which : event.charCode);
      if (!regex.test(key)) {
        event.preventDefault();
        return false;
      }
    });

    inputProductQuantity.addEventListener(
      'input',
      this.handledProductQuantityChange,
      defaultEventsConfig
    );

    if (inputLiftingCheckbox) {
      inputLiftingCheckbox.addEventListener(
        'change',
        this.handledLiftingProduct,
        defaultEventsConfig
      );
    }

    if (inputSpecialPackagingCheckbox) {
      inputSpecialPackagingCheckbox.addEventListener(
        'change',
        this.handledSpecialPackagingProduct,
        defaultEventsConfig
      );
    }

    if (inputDisassemblyAndAssemblyCheckbox) {
      inputDisassemblyAndAssemblyCheckbox.addEventListener(
        'change',
        this.handledDisassemblyAndAssembly,
        defaultEventsConfig
      );
    }
  };

  watchState = () => {
    const w = watch(this.store.getState, ['products', this.productId]);

    this.store.subscribe(
      w((state, prevState) => {
        if (prevState.quantity !== state.quantity) {
          this.updateQuantity();
        }

        if (prevState.requiresLifting !== state.requiresLifting) {
          this.updateRequiresLifting();
        }

        if (prevState.specialPackaging !== state.specialPackaging) {
          this.updateSpecialPackaging();
        }

        if (
          prevState.requiresAssembly !== state.requiresAssembly ||
          prevState.requiresDisassembly !== state.requiresDisassembly
        ) {
          this.updateRequiresDisassemblyAndAssembly();
        }
      })
    );
  };

  updateQuantity = () => {
    const { quantity, requiresLifting } = this.getState();

    const productElements = [
      ...document.querySelectorAll(
        `.product[data-product-id="${this.productId}"]`
      ),
    ];

    productElements.forEach(productElement => {
      const inputProductQuantity = productElement.querySelector(
        '.product__quantity'
      );

      productElement.dataset.productQuantity = quantity;
      inputProductQuantity.value = quantity;
    });

    const liftingQuantity = requiresLifting ? 1 * quantity : 0;

    const liftingQuantities = {
      'iamento na origem e destino': {
        liftingQuantityOrigin: liftingQuantity,
        liftingQuantityDestination: liftingQuantity,
      },
      'iamento no origem': {
        liftingQuantityOrigin: liftingQuantity,
      },
      'iamento no destino': {
        liftingQuantityDestination: liftingQuantity,
      },
    }[this.lifting];

    const action = productsActions.updateLiftingQuantity({
      ...liftingQuantities,
      liftingQuantity,
      productId: this.productId,
    });

    this.store.dispatch(action);
  };

  updateRequiresLifting = () => {
    const { requiresLifting, quantity } = this.getState();

    const productElements = [
      ...document.querySelectorAll(
        `.product[data-product-id="${this.productId}"]`
      ),
    ];

    productElements.forEach(productElement => {
      productElement.dataset.productLifting = requiresLifting;
    });

    const action = productsActions.updateLiftingQuantity({
      productId: this.productId,
      liftingQuantity: requiresLifting ? 1 * quantity : 0,
    });

    this.store.dispatch(action);
  };

  updateSpecialPackaging = () => {
    const { specialPackaging, skuIdWithBox, skuIdWithoutBox } = this.getState();

    const productElements = [
      ...document.querySelectorAll(
        `.product[data-product-id="${this.productId}"]`
      ),
    ];

    productElements.forEach(productElement => {
      productElement.dataset.productSpecialPackaging = specialPackaging;
    });

    const skuId = specialPackaging ? skuIdWithBox : skuIdWithoutBox;

    const action = productsActions.updateSkuId(this.productId, skuId);

    this.store.dispatch(action);
  };

  updateRequiresDisassemblyAndAssembly = () => {
    const { requiresAssembly, requiresDisassembly } = this.getState();

    const productElements = [
      ...document.querySelectorAll(
        `.product[data-product-id="${this.productId}"]`
      ),
    ];

    productElements.forEach(productElement => {
      productElement.dataset.productRequiresAssembly = requiresAssembly;
      productElement.dataset.productRequiresDisassembly = requiresDisassembly;
    });
  };

  render = () => {
    const {
      isCustom,
      quantity,
      requiresLifting,
      specialPackaging,
      skuIdWithBox,
      skuIdWithoutBox,
      requiresAssembly,
      requiresDisassembly,
    } = this.getState();

    return `
      <article 
        class="product" 
        data-product-id="${this.productId}"
        data-product-quantity="${quantity}" 
        data-sku-id-with-box="${skuIdWithBox}"
        data-sku-id-without-box="${skuIdWithoutBox}"
        data-product-lifting="${requiresLifting}"
        data-product-special-packaging="${specialPackaging}" 
        data-product-complement-name="${this.complementName}"
        data-product-categories-ambients="${this.ambients}"
        data-product-similars="${this.similars}"
        data-product-requires-assembly="${requiresAssembly}"
        data-product-requires-disassembly="${requiresDisassembly}"
      >
        ${(this.assignments &&
          this.assignments.includes('iamento') &&
          ProductCheckboxComponent({
            id: `product-${this.productId}-lifting`,
            isChecked: requiresLifting,
            labelText: 'Requer iamento',
            tooltipText: liftingTooltipText,
            tooltipClassName: 'without-padding',
            tooltipPosition: 'left',
            tooltipSize: 'medium',
          })) ||
          ''}

        ${(this.assignments &&
          this.assignments.includes('embalagem especial') &&
          ProductCheckboxComponent({
            id: `product-${this.productId}-special-packaging`,
            isChecked: specialPackaging,
            labelText: 'Embalagem especial',
            tooltipText: specialPackagingTooltipText,
            tooltipClassName: 'without-padding',
            tooltipPosition: 'left',
            tooltipSize: 'medium',
          })) ||
          ''}

        ${(this.assignments &&
          this.assignments.includes('Adicional de desmontagem') &&
          ProductCheckboxComponent({
            id: `product-${this.productId}-disassembly-and-assembly`,
            isChecked: requiresAssembly && requiresDisassembly,
            labelText: 'Desmontagem e montagem',
            tooltipText: disassemblyAndAssemblyTooltipText,
            tooltipClassName: 'without-padding',
            tooltipPosition: 'left',
            tooltipSize: 'medium',
          })) ||
          ''}

        ${
          isCustom
            ? CustomProductImageComponent()
            : ProductImageComponent(this.imageUrl, this.productName)
        }

        <h3 class="product__name">${this.productName}</h3>

        ${ProductActionsComponent({ quantity })}
      </article>
    `;
  };
}
